// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false

// Instance properties used for http requests
Vue.prototype.$url1 = 'https://radiant-beach-89364.herokuapp.com/movies/'
Vue.prototype.$url2 = 'https://radiant-beach-89364.herokuapp.com/categories/'
Vue.prototype.$url3 = 'https://radiant-beach-89364.herokuapp.com/new'
Vue.prototype.$urlSettings = {
  method: 'GET',
  headers: {
    Accept: 'application/json'
  }
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



